@import url('https://cdn.jsdelivr.net/gh/hgyoseo/ridibatang@master/font.css');
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700|Roboto+Slab:400,700');
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,700|Noto+Serif+KR:400,700');

.App {
  width: 100%;
  max-width: 1200px;
  min-width: 600px;
  text-align: justify;
  font-family: RIDIBatang, "Roboto Slab", "Noto Serif KR", serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 0;
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  font-size: calc(16px); /*(10px + 2vmin);*/
  color: #000000;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
